.video-container {
    display: flex;
    flex-wrap: wrap;
    width: 100vw;
    height: 100vh;
}
  
  .video {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center;
    max-width: 100%; /* Videos won't exceed container width */
  }
  

  