.ascii-container {
    padding-top: 5%;
    display: flex;
    justify-content: center;
}

.ascii-body {
    width: fit-content;
    padding-top: 1%; /* Adjust the padding as needed */
    font-family: monospace;
    font-size: 5px;
    color: white;
    background-color: black;
    white-space: pre;
    box-sizing: border-box; /* Ensure padding and border are included in the width calculation */
}

.loading {
    padding-top: 30%;
    font-size: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}


/* Adjustments for smaller screens */
@media (max-width: 600px) {
    .ascii-body {
        padding-top: 2%;
        font-size: 3px;
    }

    .loading {
        font-size: 4vw;
    }
    .loading img {
        max-width: 50%;
        max-height: 50%;
    }
}

@media (max-width: 400px) {
    .ascii-body {
        padding-top: 2%;
        font-size: 2px;
    }

    .loading {
        font-size: 3vw;
    }
    .loading img {
        max-width: 25%;
        max-height: 25%;
    }
}
